body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.text-area {
    width: 300px;
}

.comment {
    height: 2rem;
    width: 300px;
    margin-bottom: 10px;
}

.comment-button {
    font-size: x-small;
}

table tr {
    border: solid 1px gray;
}

table tr th {
    font-size: small;
    color: gray;
}

table tr td {
    word-break: break-all;  /* Break the long sentence into several lines */
}

table {
    border-collapse: collapse;
}


